import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getMisConvocatorias(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/recursos-estudiante/show-convocatoria-becas', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

  },
}
